.scrollContainer{overflow-y:auto;height:96%;width:98%;margin-top:10px;margin-left:13px;margin-right:5px}.scrollContainer::-webkit-scrollbar{width:8px}.scrollContainer::-webkit-scrollbar-thumb{background:#353535;border-radius:16px}.scrollContainer::-webkit-scrollbar-thumb:hover{background:#dadada}

.pinWrapper{display:inline-block;width:50px;margin-left:35px;vertical-align:middle}.pin{width:30px;height:30px;border-radius:50% 50% 50% 0;background:#DBDBDB;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);-webkit-animation-name:bounce;animation-name:bounce;-webkit-animation-fill-mode:both;animation-fill-mode:both;-webkit-animation-duration:1s;animation-duration:1s}.pin:after{content:'';width:14px;height:14px;margin:8px 0 0 8px;background:#2f2f2f;position:absolute;border-radius:50%}.pulse{background:rgba(0,0,0,0.2);border-radius:50%;height:14px;width:14px;margin:11px 0px 0px 8px;-webkit-transform:rotateX(55deg);transform:rotateX(55deg)}.pulse:after{content:"";border-radius:50%;height:40px;width:40px;position:absolute;margin:-13px 0 0 -13px;-webkit-animation:pulsate 1s ease-out;animation:pulsate 1s ease-out;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;opacity:0;-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";filter:alpha(opacity=0);box-shadow:0 0 1px 2px #89849b;-webkit-animation-delay:1.1s;animation-delay:1.1s}@-webkit-keyframes pulsate{0%{-webkit-transform:scale(0.1, 0.1);transform:scale(0.1, 0.1);opacity:0;-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";filter:alpha(opacity=0)}50%{opacity:1;-ms-filter:none;-webkit-filter:none;filter:none}100%{-webkit-transform:scale(1.2, 1.2);transform:scale(1.2, 1.2);opacity:0;-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";filter:alpha(opacity=0)}}@keyframes pulsate{0%{-webkit-transform:scale(0.1, 0.1);transform:scale(0.1, 0.1);opacity:0;-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";filter:alpha(opacity=0)}50%{opacity:1;-ms-filter:none;-webkit-filter:none;filter:none}100%{-webkit-transform:scale(1.2, 1.2);transform:scale(1.2, 1.2);opacity:0;-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";filter:alpha(opacity=0)}}@-webkit-keyframes bounce{0%{opacity:0;-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";filter:alpha(opacity=0);-webkit-transform:translateY(-2000px) rotate(-45deg);transform:translateY(-2000px) rotate(-45deg)}60%{opacity:1;-ms-filter:none;-webkit-filter:none;filter:none;-webkit-transform:translateY(30px) rotate(-45deg);transform:translateY(30px) rotate(-45deg)}80%{-webkit-transform:translateY(-10px) rotate(-45deg);transform:translateY(-10px) rotate(-45deg)}100%{-webkit-transform:translateY(0) rotate(-45deg);transform:translateY(0) rotate(-45deg)}}@keyframes bounce{0%{opacity:0;-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";filter:alpha(opacity=0);-webkit-transform:translateY(-2000px) rotate(-45deg);transform:translateY(-2000px) rotate(-45deg)}60%{opacity:1;-ms-filter:none;-webkit-filter:none;filter:none;-webkit-transform:translateY(30px) rotate(-45deg);transform:translateY(30px) rotate(-45deg)}80%{-webkit-transform:translateY(-10px) rotate(-45deg);transform:translateY(-10px) rotate(-45deg)}100%{-webkit-transform:translateY(0) rotate(-45deg);transform:translateY(0) rotate(-45deg)}}

.aboutOuterBox{position:absolute;width:90%;height:90%;left:5%;top:5%;background-color:#464646;display:flex;flex-flow:row wrap}.aboutIconWrapper{order:1;flex:100% 1;margin-top:25px;text-align:center}.aboutNameWrapper{order:2;flex:100% 1;text-align:center;font-family:"Chivo",serif;color:#ffff;margin-top:10px;font-size:40px}.aboutLocationWrapper{order:3;flex:100% 1;font-family:"Chivo",serif;color:#ffff;font-size:25px;display:block;position:relative;left:35%;width:30%;margin-top:15px}.aboutLocationLink{display:inline;line-height:80px;vertical-align:middle;color:#fff}.aboutLocationLink:hover{font-size:27px;color:#fff;text-decoration:none}.aboutTextCrawl{order:4;flex:100% 1;text-align:center;font-family:"Chivo",serif;color:#ffff;margin-top:40px;font-size:20px;display:block;position:relative;left:5%;width:90%}.aboutTextQuestion{margin-top:40px;margin-bottom:20px;font-size:25px;font-weight:700}.aboutTextAnswer{margin-bottom:80px;font-size:18px}.aboutDivider{display:block;position:relative;margin:30px 0 50px 0;left:10%;width:80%;border-bottom:1px solid rgba(255,255,255,0.596);height:10px}@media only screen and (min-width: 601px) and (max-width: 1500px){.aboutLocationWrapper{order:3;flex:100% 1;font-family:"Chivo", serif;color:#ffff;font-size:2.2vw;display:block;position:relative;left:18vw;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-top:15px}.aboutLocationLink{display:inline;line-height:80px;vertical-align:middle;color:#fff;margin-left:4vw}.aboutLocationLink:hover{font-size:2.2vw;color:#fff;text-decoration:none}}@media only screen and (min-width: 601px) and (max-width: 850px){.aboutLocationWrapper{left:12vw}}@media only screen and (max-width: 600px){.aboutOuterBox{position:fixed;width:100%;height:auto;left:0px;top:60px;bottom:90px;background-color:#464646;display:flex;flex-flow:row wrap}.aboutLocationWrapper{order:3;flex:100% 1;font-family:"Chivo",serif;color:#ffff;font-size:5vw;display:block;position:relative;left:15%;width:70%;margin-top:15px}.aboutLocationLink{display:inline;line-height:80px;vertical-align:middle;color:#fff;margin-left:4vw}.aboutLocationLink:hover{font-size:5vw;color:#fff;text-decoration:none}}@media only screen and (max-width: 450px){.aboutLocationWrapper{order:3;flex:100% 1;font-family:"Chivo",serif;color:#ffff;font-size:5vw;display:block;position:relative;left:5%;width:90%;margin-top:15px}.aboutLocationLink{display:inline;line-height:80px;vertical-align:middle;color:#fff;margin-left:5vw}.aboutLocationLink:hover{font-size:5vw;color:#fff;text-decoration:none}.aboutNameWrapper{order:2;flex:100% 1;text-align:center;font-family:"Chivo",serif;color:#ffff;margin-top:10px;font-size:30px}}

